import React, { useState, useEffect } from "react";
import axios from "axios";
import styled from "styled-components";
import AdminHeader from "../../components/AdminHeader";

const AdminFilter = () => {
  const [title, setTitle] = useState("");
  const [regionList, setRegionList] = useState([]);
  const [regionSelected, setRegionSelected] = useState("전국");
  const [themeList, setThemeList] = useState([]);
  const [themeSelected, setThemeSelected] = useState("전체");

  const onChangeTitle = (e) => {
    setTitle(e.target.value);
  };
  const titleSearchEvent = () => {
    console.log(title);
  };
  const enterEvent = (e) => {
    if (e.key === "Enter") {
      titleSearchEvent();
    }
  };

  const getCategory = async () => {
    await axios({
      method: "get",
      url: `${process.env.REACT_APP_API_URL}/v2/place/category`,
    })
      .then((res) => {
        console.log(res.data);
        if (res.data.isPass) {
          setRegionList(res.data.regionList);
          setThemeList(res.data.themeList);
        }
      })
      .catch((err) => {
        console.log(err);
      });
  };

  useEffect(() => {
    getCategory();
  }, []);

  return (
    <Container>
      <AdminHeader />
      <ContentContainer>
        <TitleInputArea>
          <TitleInput
            type="text"
            placeholder="여행지를 입력해주세요."
            onChange={onChangeTitle}
            onKeyUp={enterEvent}
          />
          <TitleSearchBtn
            src={require("../../assets/image/graySearchIcon.png")}
            onClick={() => titleSearchEvent()}
          />
        </TitleInputArea>
        <CategoryContainer>
          {regionList.map((item, idx) => {
            return (
              <CategoryBtn
                key={idx}
                style={regionSelected === item ? { color: "#fff", backgroundColor: "#25b1fa" } : {}}
                onClick={() => setRegionSelected(item)}
              >
                {item}
              </CategoryBtn>
            );
          })}
        </CategoryContainer>
        <CategoryContainer>
          {themeList.map((item, idx) => {
            return (
              <CategoryBtn
                key={idx}
                style={themeSelected === item ? { color: "#fff", backgroundColor: "#25b1fa" } : {}}
                onClick={() => setThemeSelected(item)}
              >
                {item}
              </CategoryBtn>
            );
          })}
        </CategoryContainer>
      </ContentContainer>
    </Container>
  );
};
const Container = styled.div`
  display: flex;
  flex-direction: column;
  width: 1920px;
  height: 100%;
  margin: 0 auto;
  justify-content: center;
  position: relative;
  padding: 0px 50px;
`;
const ContentContainer = styled.div`
  margin-top: 40px;
  display: flex;
  flex-direction: column;
  margin: 40px 40px;
`;
const TitleInputArea = styled.div`
  width: 612px;
  height: 40px;
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
`;
const TitleInput = styled.input`
  width: 100%;
  height: 100%;
  font-size: 14px;
  padding: 7.5px 30px 7.5px 10px;
  border-radius: 8px;
  border: 1px solid #ddd;
`;
const TitleSearchBtn = styled.img`
  position: absolute;
  right: 7.5px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 25px;
  height: 25px;
  cursor: pointer;
`;
const CategoryContainer = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 14px;
`;
const CategoryBtn = styled.button`
  display: flex;
  align-items: center;
  justify-content: center;
  height: 32px;
  padding: 0px 20px;
  background-color: #fff;
  border: 1px solid #25b1fa;
  border-radius: 8px;
  margin-right: 8px;
  color: #25b1fa;
`;

export default AdminFilter;
